      <img src="@/assets/img/logo01.png" alt="" class="logo">
    <!-- </router-link> -->
    <router-link to="/" class="log_out">로그아웃</router-link>
    <div class="header row">
        <h2>{{user_nickname}} 님!! 샘플 문제를 만들수 있습니다.</h2>
        <div class="btn">
            <router-link to="/making" v-show="make" >문제 만들기</router-link>
            <router-link to="/savelist" v-show="usermake" @click="select_option('complete_time')">나의 문제집</router-link>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      make: false,
      usermake: false,
      topLogo: false,
      ment: false
    }
  },
  methods: {
    makeControl () {
      this.make = !this.make
    },
    usermakeControl () {
      this.usermake = !this.usermake
    },
    topLogoControl () {
      this.topLogo = !this.topLogo
    },
    mentControl () {
      this.ment = !this.ment
    },
    select_option (data) {
      this.$store.commit('select_option', data)
    }
  },
  computed: {
    user_nickname () {
      return this.$store.state.now_user.nickname
    }
  },
  created () {
    if (!this.$store.state.now_user.nickname) {
      alert('로그인 후 이용해 주세요')
      this.$router.push({ path: '/' })
    }
  }
}
</script>
